<script>
import { ref, onMounted, onUnmounted } from 'vue'
import Header from "@/components/global/header/HeaderArea.vue"
import { getUrlId, errorHandle, addZero } from '@/utils/handle'
import { Toast } from '../../components/global/swal'
import { apiGetScheduleInfo, apiPostScheduleComment, apiGetPinkScheduleInfo, apiPostPunchIn, apiPostPunchOut } from "@/api/api"

export default {
  setup() {
    let mode = ref('purple')
    let scheduleData = ref()
    const urlId = getUrlId().urlId
    let starClass = ref(["fas fa-star active", "fas fa-star", "fas fa-star", "fas fa-star", "fas fa-star"])
    let postData = ref({
      "scheduleId": urlId,
      "star": 1,
      "comment": ""
    })
    let punchData = ref({
      "time": null,
      "reason": null
    })
    const nowTime = ref({})

    onMounted(async () => {
      try {
        if (process.env.VUE_APP_MODE == 'purple') {
          mode.value = 'purple'
          scheduleData.value = await apiGetScheduleInfo({ id: urlId })
        } else {
          mode.value = 'pink'
          scheduleData.value = await apiGetPinkScheduleInfo({ id: urlId })
        }
        scheduleData.value = scheduleData.value.data.data
        //看護language
        scheduleData.value.checkedCaretaker.languageInfo = []
        if (scheduleData.value.checkedCaretaker.language?.length > 0) {
          scheduleData.value.checkedCaretaker.language.forEach(item => {
            scheduleData.value.checkedCaretaker.languageInfo.push(item.name)
          })
        }
        //看護 experienceInfo
        scheduleData.value.checkedCaretaker.experienceInfo = []
        if (scheduleData.value.checkedCaretaker.experience?.length > 0) {
          scheduleData.value.checkedCaretaker.experience.forEach(item => {
            scheduleData.value.checkedCaretaker.experienceInfo.push(item.careExperience)
          })
        }
        //看護 workPlaceInfo
        scheduleData.value.checkedCaretaker.workPlaceInfo = []
        if (scheduleData.value.checkedCaretaker.workPlace?.length > 0) {
          scheduleData.value.checkedCaretaker.workPlace.forEach(item => {
            scheduleData.value.checkedCaretaker.workPlaceInfo.push(`${item.city}${item.area}`)
          })
        }
        //eatTypeInfo
        // scheduleData.value.disabledPerson.eatTypeInfo = []
        // if (scheduleData.value.disabledPerson.eatType?.length > 0) {
        //   scheduleData.value.disabledPerson.eatType.forEach(item => {
        //     scheduleData.value.disabledPerson.eatTypeInfo.push(item.name)
        //   })
        // }
        //abilityToActInfo
        scheduleData.value.disabledPerson.abilityToActInfo = []
        if (scheduleData.value.disabledPerson.abilityToAct?.length > 0) {
          scheduleData.value.disabledPerson.abilityToAct.forEach(item => {
            scheduleData.value.disabledPerson.abilityToActInfo.push(item.name);
          })
        }
        //servicePlaceInfo
        scheduleData.value.disabledPerson.servicePlaceInfo = scheduleData.value.disabledPerson.servicePlace.city.name + scheduleData.value.disabledPerson.servicePlace.area.name + " " + scheduleData.value.disabledPerson.servicePlace.place.name
        //serviceInfo
        scheduleData.value.employerService.serviceInfo = []
        if (scheduleData.value.employerService.service?.length > 0) {
          scheduleData.value.employerService.service.forEach(item => {
            scheduleData.value.employerService.serviceInfo.push(item.name)
          })
        }
        //timePeriod工作時段
        scheduleData.value.checkedCaretaker.timePeriodInfo = []
        scheduleData.value.checkedCaretaker.timePeriod.forEach(item => {
          scheduleData.value.checkedCaretaker.timePeriodInfo.push(item.name);
        });
        scheduleData.value.checkedCaretaker.timePeriodInfo.reverse()
        //  
        if (scheduleData.value.checkedCaretaker.otherJobTime != 0) {
          scheduleData.value.timePeriodInfo.push(`${scheduleData.value.checkedCaretaker.otherJobTime}小時`);
        }

      } 
      catch (error) {
        console.log(error)
      }

      // console.log(scheduleData.value)
    })


    const starBtn = (val) => {
      if (val == 1) {
        starClass.value = ["fas fa-star active", "fas fa-star", "fas fa-star", "fas fa-star", "fas fa-star"]
      } else if (val == 2) {
        starClass.value = ["fas fa-star active", "fas fa-star active", "fas fa-star", "fas fa-star", "fas fa-star"]
      } else if (val == 3) {
        starClass.value = ["fas fa-star active", "fas fa-star active", "fas fa-star active", "fas fa-star", "fas fa-star"]
      } else if (val == 4) {
        starClass.value = ["fas fa-star active", "fas fa-star active", "fas fa-star active", "fas fa-star active", "fas fa-star"]
      } else if (val == 5) {
        starClass.value = ["fas fa-star active", "fas fa-star active", "fas fa-star active", "fas fa-star active", "fas fa-star active"]
      }
      postData.value.star = val
    }

    const checkFinish = async () => {
      try {
        // console.log(postData.value)
        let msg = await apiPostScheduleComment({ id: urlId }, postData.value)
        if (msg.status == true) {
          Toast("已確認完工")
          scheduleData.value.workingStatus = 1
        } else {
          let errMessage = errorHandle(msg)
          Toast(errMessage)
        }
      } catch (error) {
        console.log(error)
      }
    }

    const getNowTime = () => {
      let today = new Date()
      nowTime.value.year = addZero(today.getFullYear())
      nowTime.value.month = addZero(today.getMonth() + 1)
      nowTime.value.date = addZero(today.getDate())
      nowTime.value.hh = addZero(today.getHours())
      nowTime.value.mm = addZero(today.getMinutes())
      nowTime.value.ss = addZero(today.getSeconds())
      nowTime.value.day = addZero(today.getDay())
      nowTime.value.text = `${nowTime.value.year}-${nowTime.value.month}-${nowTime.value.date} ${nowTime.value.hh}:${nowTime.value.mm}`
      if (nowTime.value.day == 0) {
        nowTime.value.day = "星期天"
      } else if (nowTime.value.day == 1) {
        nowTime.value.day = "星期一"
      } else if (nowTime.value.day == 2) {
        nowTime.value.day = "星期二"
      } else if (nowTime.value.day == 3) {
        nowTime.value.day = "星期三"
      } else if (nowTime.value.day == 4) {
        nowTime.value.day = "星期四"
      } else if (nowTime.value.day == 5) {
        nowTime.value.day = "星期五"
      } else if (nowTime.value.day == 6) {
        nowTime.value.day = "星期六"
      }
      // console.log(nowTime.value)
    }

    const getTime = setInterval(() => {
      getNowTime()
    }, 1000);

    const punchIn = async () => {
      if (scheduleData.value.punchOutType == 2 && !punchData.value.reason) {
        Toast("請填寫補打卡理由")
      } else {
        punchData.value.time = nowTime.value.text
        scheduleData.value.punchInTime = nowTime.value.text.replace(/-/g, '/')
        // console.log(punchData.value)
        try {
          let msg = await apiPostPunchIn({ id: urlId }, punchData.value)
          if (msg.status == true) {
            Toast("已完成簽到打卡")
            scheduleData.value.punchInType = 1
          } else {
            let errMessage = errorHandle(msg)
            Toast(errMessage)
          }
        } catch (error) {
          console.log(error)
        }
      }
    }

    const punchOut = async () => {
      if (!scheduleData.value.punchInTime) {
        Toast("請先完成簽到打卡")
      } else {
        if (scheduleData.value.punchOutType == 2 && !punchData.value.reason) {
          Toast("請填寫補打卡理由")
        } else {
          punchData.value.time = nowTime.value.text.replace(/-/g, '/')
          scheduleData.value.punchOutTime = nowTime.value.text
          try {
            let msg = await apiPostPunchOut({ id: urlId }, punchData.value)
            if (msg.status == true) {
              Toast("已完成簽退打卡")
              scheduleData.value.punchOutType = 1
            } else {
              let errMessage = errorHandle(msg)
              Toast(errMessage)
            }
          } catch (error) {
            console.log(error)
          }
        }
      }
    }

    onUnmounted(() => clearInterval(getTime))


    return {
      mode, getTime, nowTime, punchData, punchIn, punchOut,
      scheduleData, starBtn, starClass, postData, checkFinish
    }
  },
  components: {
    Header
  },
}

</script>

<template>
  <Header title="排程內容" backToPath="/schedule/list" />
  <div class="scheduleInfo">
    <section class="c-main" v-if="(scheduleData?.workOrderId)">
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">該日排程</div>
          <div class="item-content">
            <div class="main2">
              <div class="title2">日期時段</div>
              <div class="content2">{{ scheduleData.startTime.substring(0, scheduleData.startTime.length - 3) }} ~ {{
                  scheduleData.endTime.substring(0, scheduleData.endTime.length - 3)
              }}</div>
            </div>
            <div class="main2" v-if="(mode == 'purple')">
              <div class="title2">簽到打卡</div>
              <div class="content2">
                <span v-if="(scheduleData?.punchInTime?.length > 0)">簽到 - <span class="check">{{
                    scheduleData.punchInTime.substring(0, scheduleData.punchInTime.length - 3)
                }}</span></span>
                <span v-if="(!scheduleData?.punchInTime?.length > 0)">簽到 - <span class="noCheck">未打卡</span></span>
                <div class="checkArea">
                  <span v-if="(scheduleData?.punchOutTime?.length > 0)">簽退 - <span class="check">{{
                      scheduleData.punchOutTime.substring(0, scheduleData.punchOutTime.length - 3)
                  }}</span></span>
                  <span v-if="(!scheduleData?.punchOutTime?.length > 0)">簽退 - <span class="noCheck">未打卡</span></span>
                  <a href="javascript:void(0);" class="btn btn-state card-state" data-bs-toggle="modal"
                    data-bs-target="#openWork" v-if="(scheduleData.workingStatus == 3)">確認完工</a>
                </div>
              </div>
            </div>
            <div class="main2" v-if="(mode == 'pink')">
              <div class="title2">簽到打卡</div>
              <div class="content2">
                <span v-if="(scheduleData.punchInType == 1) || (scheduleData.punchInType == 3)">簽到 - <span
                    class="check">{{
                        scheduleData.punchInTime.substring(0, 16)
                    }}</span></span>
                <span v-if="(scheduleData.punchInType == 0) || (scheduleData.punchInType == 2)">簽到 - <span
                    class="noCheck">未打卡</span></span>
                <div class="checkArea">
                  <span v-if="(scheduleData.punchOutType == 1) || (scheduleData.punchOutType == 3)">簽退 - <span
                      class="check">{{
                          scheduleData.punchOutTime.substring(0, 16)
                      }}</span></span>
                  <span v-if="(scheduleData.punchOutType == 0) || (scheduleData.punchOutType == 2)">簽退 - <span
                      class="noCheck">未打卡</span></span>
                  <a href="javascript:void(0);" class="btn btn-state card-state" data-bs-toggle="modal"
                    data-bs-target="#openClock"
                    v-if="(scheduleData.workingStatus == 0) || (scheduleData.workingStatus == 2)">打卡</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse1">
            {{ scheduleData.checkedCaretaker.name }}</div>
          <div class="item-content collapse" id="collapse1">
            <div>
              <div class="basicInfo">
                <div class="infoText">
                  <div class="main1">
                    <div class="title1">性別</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.gender == 'Male' ? '男性' : '女性' }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">年齡</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.age }}歲</div>
                  </div>
                  <div class="main1">
                    <div class="title1">身高</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.height }}公分</div>
                  </div>
                  <div class="main1">
                    <div class="title1">體重</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.weight }}公斤</div>
                  </div>
                  <div class="main1">
                    <div class="title1">年資</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.seniority }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">國籍</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.countryCode }}</div>
                  </div>
                  <div class="main1">
                    <div class="title1">語言</div>
                    <div class="content1">{{ scheduleData.checkedCaretaker.languageInfo.join('、') }}</div>
                  </div>
                </div>
                <img :src='scheduleData.checkedCaretaker.avatarUrl' alt="看護照片"
                  v-if="scheduleData.checkedCaretaker.avatarUrl" onerror="this.onerror=null; this.src='https://fakeimg.pl/120x160/'">
                <img src='@/assets/image/noavatar.jpg' alt="看護照片" v-if="!scheduleData.checkedCaretaker.avatarUrl">
              </div>
              <div class="main2">
                <div class="title2">履歷身分</div>
                <div class="content2">{{ scheduleData.serviceName }}</div>
              </div>
              <div class="main2">
                <div class="title2">具備證照</div>
                <div class="content2">{{ scheduleData.checkedCaretaker.license.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">收費標準</div>
                <div class="content2" v-if="scheduleData.checkedCaretaker.wage.home">居家：NT${{
                    scheduleData.checkedCaretaker.wage.home
                }}/小時<span v-if="scheduleData.checkedCaretaker.wage?.fullDay?.home">、NT${{
    scheduleData.checkedCaretaker.wage.fullDay.home
}}/日</span></div>
                <div class="content2" v-if="scheduleData.checkedCaretaker.wage.home">醫院：NT${{
                    scheduleData.checkedCaretaker.wage.hospital
                }}/小時<span v-if="scheduleData.checkedCaretaker.wage?.fullDay?.hospital">、NT${{
    scheduleData.checkedCaretaker.wage.fullDay.hospital
}}/日</span>
                </div>
              </div>
              <div class="main2">
                <div class="title2">照顧經驗及時數</div>
                <div class="content2">{{ scheduleData.checkedCaretaker.experienceInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">服務地區</div>
                <div class="content2">{{ scheduleData.checkedCaretaker.workPlaceInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">工作時段</div>
                <div class="content2">{{ scheduleData.checkedCaretaker.timePeriodInfo.join('、') }}</div>
              </div>
              <div class="main2">
                <div class="title2">自我介紹</div>
                <div class="content2">{{ scheduleData.checkedCaretaker.selfIntroduction }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse2">被照護者資訊</div>
          <div class="item-content collapse" id="collapse2">
            <div>
              <div class="main1">
                <div class="title1">名稱</div>
                <div class="content1">{{ scheduleData.disabledPerson.name }}</div>
              </div>
              <div class="main1">
                <div class="title1">性別</div>
                <div class="content1">{{ scheduleData.disabledPerson.gender == 'Female' ? '女性' : '男性' }}</div>
              </div>
              <div class="main1">
                <div class="title1">年齡</div>
                <div class="content1">{{ scheduleData.disabledPerson.age }}歲</div>
              </div>
              <div class="main1">
                <div class="title1">身高</div>
                <div class="content1">{{ scheduleData.disabledPerson.height }}公分</div>
              </div>
              <div class="main1">
                <div class="title1">體重</div>
                <div class="content1">{{ scheduleData.disabledPerson.weight }}公斤</div>
              </div>
              <div class="main1">
                <div class="title1">關係</div>
                <div class="content1">{{ scheduleData.disabledPerson.relationship }}</div>
              </div>
              <!-- <div class="main2">
                <div class="title2">是否有傳染力</div>
                <div class="content2">{{ scheduleData.disabledPerson.isContagious ? "是" : "否" }}</div>
              </div>
              <div class="main2">
                <div class="title2">飲食類型</div>
                <div class="content2" v-if="(scheduleData?.disabledPerson?.eatTypeInfo?.length != 0)">{{
                    scheduleData?.disabledPerson?.eatTypeInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(scheduleData?.disabledPerson?.eatTypeInfo?.length == 0)">無</div>
              </div> -->
              <div class="main2">
                <div class="title2">行動能力</div>
                <div class="content2" v-if="(scheduleData?.disabledPerson?.abilityToActInfo?.length != 0)">{{
                    scheduleData?.disabledPerson?.abilityToActInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(scheduleData?.disabledPerson?.abilityToActInfo?.length == 0)">無</div>
              </div>
              <div class="main2">
                <div class="title2">照顧地點</div>
                <div class="content2">{{ scheduleData.disabledPerson.servicePlaceInfo }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse3">病症</div>
          <div class="item-content collapse" id="collapse3">
            <div class="main2" v-for="item in scheduleData?.disabledPerson?.disease" :key="item.id">
              <div class="title2">{{ item.diseaseName }}</div>
              <div class="content2">{{ item.symptom.join('、') }}</div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse4">服務需求</div>
          <div class="item-content collapse" id="collapse4">
            <div class="content2">{{ scheduleData.employerService?.serviceInfo?.join('、') }}</div>
          </div>
        </div>
        <!-- 確認完工彈窗 -->
        <div class="modal fade" id="openWork" tabindex="-1" aria-labelledby="openModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-body">
                <div class="form-container form-container-2">
                  <div class="mb-3"
                    v-if="(scheduleData.reason?.punchInReason?.length != 0) || (scheduleData.reason?.punchOutReason?.length != 0)">
                    <label class="form-label">理由<span class="star"></span></label>
                    <div class="form-content">{{ scheduleData?.reason?.punchInReason }}</div>
                    <div class="form-content">{{ scheduleData?.reason?.punchOutReason }}</div>
                  </div>
                  <div class="mb-3">
                    <label class="form-label">評價<span class="star">*</span></label>
                    <div id="rateBox" class="rateClass"></div>
                    <span class="average"></span><span class="star"><i :class=starClass[0] @click="starBtn(1)"></i><i
                        :class=starClass[1] @click="starBtn(2)"></i><i :class=starClass[2] @click="starBtn(3)"></i><i
                        :class=starClass[3] @click="starBtn(4)"></i><i :class=starClass[4]
                        @click="starBtn(5)"></i></span>
                  </div>
                  <div class="mb-1">
                    <label class="form-label">評語<span class="star"></span></label>
                    <textarea class="form-control" rows="5" placeholder="請輸入評語" v-model="postData.comment"></textarea>
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <div class="btn-container">
                  <div class="row">
                    <div class="col">
                      <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal"
                        @click="checkFinish">確認完工</button>
                    </div>
                    <div class="col">
                      <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal">取消關閉</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- 打卡彈窗 -->
        <div class="modal fade" id="openClock" tabindex="-1" aria-labelledby="openModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-body">
                <div class="punch-container">
                  <div class="clock-container">
                    <div class="clock-content">
                      <div id="clock">{{ nowTime.hh }}:{{ nowTime.mm }}:{{ nowTime.ss }}</div>
                      <div class="date">{{ nowTime.year }}/{{ nowTime.month }}/{{ nowTime.date }} {{ nowTime.day }}
                      </div>
                    </div>
                  </div>
                  <!-- 補充日期時段 -->
                  <div class="mb-1">
                    <div class="main2">
                      <label class="form-label">該日工作時間</label>
                      <div class="content2">{{ scheduleData.startTime.substring(0, scheduleData.startTime.length - 3) }} ~ {{
                          scheduleData.endTime.substring(0, scheduleData.endTime.length - 3)
                      }}</div>
                    </div>
                  </div>     
                  <!--  -->
                  <div class="mb-1" v-if="(scheduleData.punchInType == 2) || (scheduleData.punchOutType == 2)">
                    <label class="form-label">理由<span class="star"></span></label>
                    <textarea class="form-control" rows="5" placeholder="請輸入補打卡理由" v-model="punchData.reason"
                      required></textarea>
                  </div>
                  <div class="btn-container">
                    <div class="row">
                      <div class="col">
                        <button class="btn btn-signin" type="button" data-bs-dismiss="modal"
                          v-if="(scheduleData.punchInType == 0) || (scheduleData.punchInType == 2)"
                          @click="punchIn">簽到</button>
                        <button class="btn btn-time" type="button" data-bs-dismiss="modal"
                          v-if="(scheduleData.punchInType == 1) || (scheduleData.punchInType == 3)">
                          {{ scheduleData.punchInTime.split(' ')[1].slice(0, 5) }}
                        </button>
                      </div>
                      <div class="col">
                        <button class="btn btn-signout" type="button" data-bs-dismiss="modal"
                          v-if="(scheduleData.punchOutType == 0) || (scheduleData.punchOutType == 2)"
                          @click="punchOut">簽退</button>
                        <button class="btn btn-time" type="button" data-bs-dismiss="modal"
                          v-if="(scheduleData.punchOutType == 1) || (scheduleData.punchOutType == 3)">
                          {{ scheduleData.punchOutTime.split(' ')[1].slice(0, 5) }}</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.scheduleInfo {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.basicInfo {
  display: flex;
  justify-content: space-between;

  img {
    height: 10rem;
    width: 7.5rem;
    border-radius: 4px;
  }

}

.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;

  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
